.connections {
  font-size: 1.6rem;
  padding: 1rem;
  background-color: #fff;
}

.connectionsHeading {
  color: #143f6b;
  text-align: center;
  margin-bottom: 2rem;
  font-weight: 900;
}

table {
  width: 100%;
}

.tableHeading {
  width: 30%;
  text-align: left;
  background-color: var(--red);
  color: white;
  padding: 1rem;
  font-weight: normal;
}
.tableData {
  width: 70%;
  padding: 1rem;
  /* border-style: none;
  border: 3px solid white; */
}

.odd {
  background-color: var(--light-orange1);
}

.even {
  background-color: var(--light-orange2);
}

@media (min-width: 500px) {
  .connections {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

@media (min-width: 730px) {
  .connectionsHeading {
    font-size: 2.2rem;
  }
}
@media (min-width: 1000px) {
  .connectionsHeading {
    font-size: 2.4rem;
  }

  .connections {
    grid-column: 1/3;
    grid-row: 5/6;
    padding: 2rem;
    font-size: 1.8rem;
  }
}
